/* ********** TOGGLE SWITCH ********* */

/*makes the size of the slider*/
.switch[data-v-9d1cd78c] {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

/* this makes the checkbox a slider*/
.slider[data-v-9d1cd78c] {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
}

/*actual slider itself*/
.slider[data-v-9d1cd78c]:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

/* set background color when active */
input:checked+.slider[data-v-9d1cd78c] {
    background-color: #2196F3;
}

/* change backround color*/
input:checked+.slider[data-v-9d1cd78c]:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
}

/* Rounded sliders border*/
.slider.round[data-v-9d1cd78c] {
    border-radius: 34px;
}

/* Rounded sliders inside circle*/
.slider.round[data-v-9d1cd78c]:before {
    border-radius: 50%;
}

